@charset "UTF-8";

input, textarea {
  -webkit-appearance: none;
  -webkit-border-radius: 0px; }

html, body {
  color: #ffffff;
  text-align: center;
  font-family: 'Lato', sans-serif;
  overflow: no-content;
  padding: 0;
  margin: 0; }

html {
  /* The image used */
  background-image: url("/assets/water-background-dark.png");


  /* Full height */
  height: 100%;

  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover; }

.logo {
  font-size: 5em;
  margin-bottom: 0; }

.tagline {
  margin: 2em;
  font-size: 2em; }

.callToAction {
  margin-top: 4em;
  display: flex;
  justify-content: center; }

