a, a:visited {
  text-decoration: none;
  color: white; }

#mc_embed_signup_scroll {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: center; }

#mc_embed_signup {
  margin: auto; }

#mc-embedded-subscribe, #mce-EMAIL {
  border: 0; }

#mce-EMAIL {
  border-bottom-left-radius: 10px;
  border-top-left-radius: 10px;
  padding-left: 1em;
  height: 46px;
  width: 17em; }

#mc-embedded-subscribe {
  border-bottom-right-radius: 10px;
  border-top-right-radius: 10px;
  background-color: #03a9f4;
  color: #FFF;
  height: 48px; }

@media screen and (max-width: 420px) {
  body {
    font-size: 10px; } }


header {
  color: white;
  background-color: #FEB13F;
  margin: 0;
  padding-left: 20px;
  height: 80px;
  display: flex;
  flex-flow: row;
  border-bottom: white 1px solid;
  font-weight: bold; }

header li {
  list-style: none;
  display: inline-block;
  margin: auto 15px;
  font-size: 1.1em;
  text-decoration: underline; }

nav {
  margin: auto 0; }


@media screen and (max-width: 600px) {
  .topnav a:not(:first-child) {
    display: none; } }

.topnav a.icon {
  float: right;
  display: block; }


@media screen and (max-width: 600px) {
  .topnav.responsive {
    position: relative; }

  .topnav.responsive a.icon {
    position: absolute;
    right: 0;
    top: 0; }

  .topnav.responsive a {
    float: none;
    display: block;
    text-align: left; } }


